* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__icon {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.header__nav {
  align-items: center;
  height: 60px;
  margin-right: 50px;
  display: flex;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-list-item {
  margin-right: 20px;
  line-height: 60px;
}

.header__nav-list-item:last-child {
  margin: 0;
}

.header__nav-list-item-link {
  text-transform: uppercase;
  color: #060b35;
  text-decoration: none;
  display: flex;
}

.header__nav-list-item-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.catalog__card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: flex;
}

.catalog__card-img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.catalog__card-details {
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 16px;
  line-height: 14px;
  display: flex;
}

.catalog__card-details-title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card-details-identifier {
  color: #616070;
  align-items: center;
  font-size: 10px;
  font-weight: 400;
}

.catalog__card-rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.catalog__card-rating-info {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.catalog__card-price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.catalog__card-price-sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.catalog__card-price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card:hover .catalog__card-details-title {
  color: #34568b;
}

.catalog__card-link {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: flex;
}

.catalog__card-link:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  width: 96px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}
